import React, { useState } from 'react'
import axios from 'axios'
import './style.css'
const LoginForm = () => {
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const [cfpassword, setcfPassword] = useState('');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [signUp, setSignUp] = useState(false)
    const handleSubmit = async (e) => {
        e.preventDefault();
        const authProject = {
            'Project-ID': '8381c33d-57cd-41b6-9374-de16a78ede49',
            'User-Name': userName,
            'User-Secret': password,
        }
        try {
            const response = await axios.get('https://api.chatengine.io/users/me', { headers: authProject})
            console.log(response)
            localStorage.setItem('username', userName);
            localStorage.setItem('password', password);
            localStorage.setItem('avatar', response.data.avatar);
            localStorage.setItem('first_name', response.data.first_name);
            localStorage.setItem('last_name', response.data.last_name); 
            window.location.reload();
        } catch (error){
            setError('Wrong username or password. Try again!');
        }
    }

    const handleSignUp =  () =>{
        setSignUp(!signUp);
    }

    const handleCreateAccount = async(e) => {
        e.preventDefault();
        const authenObject = {
            'PRIVATE-KEY': '1e5af137-df40-4f47-9963-6ef0db4afb38',
        }
        const userData = {
            "username": userName,
            "secret": password
        }
        
        if(password.localeCompare(cfpassword) != 0) setError("Your password is not confirmed");
        else {
            setError("");
            try {
                await axios.post('https://api.chatengine.io/users/',userData, {headers: authenObject})
                setSuccess("Your new account is now available!")
            } catch (error) {
                setError("The username has already been used! Try another one")
            }
        }

    }
    if(signUp){
        return(
            <div className="sign-in-form">
        <p>CHAT APP</p>
        <form onSubmit={handleCreateAccount} action="" className="first-form">
            <div className="username">
                <input type="text" name="name" id="name-input" required
                        value={userName}
                        onChange={(e) => setUserName(e.target.value)}        
                />
                <label className="content-name">
                    <span className="name-span">Username</span>
                </label>

            </div>
            <div className="password">
                <input type="password" id="password" required
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}

                />
                <label className="content-name">
                    <span className=" name-span ">Password</span>
                </label>
            </div>

            <div className="password">
                <input type="password" id="cf-password" required
                        value={cfpassword}
                        onChange={(e) => setcfPassword(e.target.value)}

                />
                <label className="content-name">
                    <span className=" name-span ">Confirm your password</span>
                </label>
            </div>
            <button id="sign-in" className="button" type="submit">Sign up</button>
        </form>
        

        <h2 className="error">{error}</h2>
        <h2 className="success">{success}</h2>
        <h4>Successfully create your account? <button className="sign-up" onClick={handleSignUp}>Sign in</button></h4>
        </div>
        )
    }
    else
    return (
        <div className="sign-in-form">
        <p>CHAT APP</p>
        <form onSubmit={handleSubmit} action="" className="first-form">
            <div className="username">
                <input type="text" name="name" id="name-input" required
                        value={userName}
                        onChange={(e) => setUserName(e.target.value)}        
                />
                <label  className="content-name">
                    <span className="name-span">Username</span>
                </label>

            </div>
            <div className="password">
                <input type="password" id="password" required
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}

                />
                <label className="content-name">
                    <span className=" name-span ">Password</span>
                </label>
            </div>
            <button id="sign-in" className="button" type="submit">Sign in</button>
        </form>
        <h4>Don't have an account? <button className="sign-up btn" onClick={handleSignUp}>Sign up</button></h4>

        <h2 className="error">{error}</h2>
        <h2 className="success">{success}</h2>
        </div>
    )
}

export default LoginForm