import {ChatEngine} from 'react-chat-engine';
import './App.css';
import LoginForm from './components/LoginForm';
import axios from 'axios'
import React, {useState} from 'react';
import Profile from './components/Profile';
import { ChatEngineWrapper, Socket, ChatSettings } from 'react-chat-engine'

function App() {
  const [inProfile, setInProfile] = useState(true)
  if(!localStorage.getItem('username')) return <LoginForm/>;
    
  else{
    const handleChatClick =  () =>{
      setInProfile(false)
    }


    const handleLogOutClick = async () =>{
  
          window.localStorage.clear();
          window.location.reload();
    }
  if(inProfile){
    return(<Profile logOutClick={handleLogOutClick} moveToChatClick={handleChatClick}/>)
  }
  else{
  

  return (
      
      <div className="App-container" >
      <ChatEngine
        height="100vh"
        projectID = '8381c33d-57cd-41b6-9374-de16a78ede49'
        userName={localStorage.getItem('username')}
        userSecret={localStorage.getItem('password')}
      />

    </div>
  )   }  
}
  }

export default App;
