import React, {useState} from 'react';
import './Profile.css'
import ImageUploader from 'react-images-upload';
import axios from 'axios';

class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            imageUrl: window.localStorage.getItem('avatar'),
            errorMessage: '',
            firstName: window.localStorage.getItem('first_name'),
            lastName: window.localStorage.getItem('last_name'),
            password: "",
            newPassword:"",
            confirmPassword: "",
            notification: ""
        }
        this.onDrop = this.onDrop.bind(this);
        this.handleFirstName = this.handleFirstName.bind(this);
        this.handleLastName = this.handleLastName.bind(this);
        this.handlePassword = this.handlePassword.bind(this);
        this.handleConfirm = this.handleConfirm.bind(this);
        this.submitInfHandler = this.submitInfHandler.bind(this);
        this.handleNewPassword = this.handleNewPassword.bind(this);
        this.submitPasswordHandler = this.submitPasswordHandler.bind(this)
    }

    async onDrop(file, success) {
        if(!file[0]) return;
        const fd = new FormData();
        fd.append('avatar', file[0], file[0].name);
        console.log(fd);

        console.log(file[0])
        const authProject = {
            'Project-ID': '8381c33d-57cd-41b6-9374-de16a78ede49',
            'User-Name': window.localStorage.getItem('username'),
            'User-Secret': window.localStorage.getItem('password'),
        }
        try {
            const response = await axios.patch('https://api.chatengine.io/users/me/', fd, {headers: authProject})
            console.log(response)
            this.setState({
                imageUrl: response.data.avatar
            })
            window.localStorage.setItem("avatar", response.data.avatar);
            
        } catch (error) {
            console.log( error.response)
        }
        
    }


    handleFirstName(e){
        e.preventDefault();
        this.setState({
            firstName: e.target.value
        })
    }

    handleLastName(e){
        e.preventDefault();
        this.setState({
            lastName: e.target.value
        })
    }

    handlePassword(e){
        e.preventDefault();
        this.setState({
            password: e.target.value
        })
    }

    handleNewPassword(e){
        e.preventDefault();
        this.setState({
            newPassword: e.target.value
        })
    }

    handleConfirm(e){
        e.preventDefault();
        this.setState({
            confirmPassword: e.target.value
        })
    }


    async submitInfHandler(e){
        e.preventDefault();

        const authProject = {
            'Project-ID': '8381c33d-57cd-41b6-9374-de16a78ede49',
            'User-Name': window.localStorage.getItem('username'),
            'User-Secret': window.localStorage.getItem('password'),
        }

        const data = {
            "first_name": this.state.firstName,
            "last_name" : this.state.lastName
        }

        try {
            const response = await axios.patch('https://api.chatengine.io/users/me/', data, {headers: authProject})
            console.log(response)
            this.setState({
                notification: "Your info has been updated successfully!"
            })
            
        } catch (error) {
            
        }
    }

    submitPasswordHandler(e){
        e.preventDefault()
    }

    render() { 
        return (
            <div className="profile-container">
                <h3 className="notif">{this.state.notification}</h3>
                <div className="setting-container">
                    <h2 className="header">Profile Settings</h2>
                    <div className="setting-content">
                        <div className="profile-picture">
                            <div className="img-container">
                                <img src={this.state.imageUrl} />
                            </div>
                            <ImageUploader
                                key="image-uploader"
                                withIcon={true}
                                singleImage={true}
                                buttonText='Choose images'
                                onChange={this.onDrop}
                                imgExtension={['.jpg', '.gif', '.png', '.gif']}
                                maxFileSize={5242880}
                            />
                        </div>
                        <div className="forms">
                        <form onSubmit={this.submitInfHandler} className="form-group">
                            <div className="form-item">
                                <div className="item-name">First Name</div>
                                <input  type="text"
                                        placeholder={window.localStorage.getItem("first_name")}
                                        value={this.state.firstName}
                                        onChange={this.handleFirstName}
                                />
                            </div>

                            <div className="form-item">
                                <div className="item-name">Last Name</div>
                                <input type="text" placeholder={window.localStorage.getItem("first_name")}
                                    value={this.state.lastName}
                                    onChange={this.handleLastName}
                                />
                            </div>

                            
                            <div className="btn-container">
                            <button className="save-btn" type="submit">Save change</button>
                            </div>
                        </form>


                        <form onSubmit={this.submitPasswordHandler} className="password-form">
                            <div className="form-item">
                            <div className="item-name">Current Password</div>
                                <input type="password"
                                    value={this.state.password}
                                    onChange={this.handlePassword}
                                />
                            </div>

                            <div className="form-item">
                            <div className="item-name">New Password</div>
                                <input type="password"
                                    value={this.state.newPassword}
                                    onChange={this.handleNewPassword}
                                />
                            </div>

                            <div className="form-item">
                                <div className="item-name">Confirm Password</div>
                                <input type="password"
                                    value={this.state.confirmPassword}
                                    onChange={this.handleConfirm}
                                />
                            </div>

                            <div className="btn-container">
                                <button className="save-btn" type="submit">Save password</button>
                            </div>
                        </form>
                        
                        </div>
                    </div>
                </div>

                <div className="actions-container">
                    <h2 className="header"> Actions</h2>
                    <div className="actions-list">
                        <div className="action">
                        <button onClick={this.props.moveToChatClick}><i className="fab fa-weixin"></i> Move to chat section</button>
                        <div className="notice">(you can hit f5 there to combe back)</div>
                        </div>
                        <div className="action">
                        <button onClick={this.props.logOutClick}><i className="fas fa-sign-out-alt"></i>Log Out</button>
                        </div>
                    </div>
                </div>
            </div>
        )

}
}
 
export default Profile;